<template>
  <div>
    <button class="edit-button" v-on:click="toggleEdit">{{edit ? 'Complete' : 'Edit'}}</button>
    <table>
      <tr>
        <th>I know</th>
        <th>AND</th>
        <th>I want to learn</th>
      </tr>
      <tr>
        <td>
          <select name="know" id="know" v-model="first">
            <option :key="t.title" v-for="t in technologies" v-bind:value="t">{{t.title}}</option>
          </select>
        </td>
        <td></td>
        <td>
          <select name="learn" id="learn" v-model="second">
            <option :key="t.title" v-for="t in technologies" v-bind:value="t">{{t.title}}</option>
          </select>
        </td>
      </tr>
      <tr class="content">
        <td>
          <ul>
            <li :key="d.id" v-for="d in uniqueFirst()">{{d.title}}</li>
          </ul>
        </td>
        <td>
          <ul>
            <li :key="d.id" v-for="d in common()">{{d.title}}</li>
          </ul>
        </td>
        <td>
          <ul>
            <li :key="d.id" v-for="d in uniqueSecond()">{{d.title}}</li>
          </ul>
        </td>
      </tr>
      <tr class="edit" v-if="edit">
        <td>
          <input type="text" v-model="firstTopic" />
          <button v-on:click="addFirst">Add</button>
        </td>
        <td>
          <input type="text" v-model="commonTopic" />
          <button v-on:click="addCommon">Add</button>
        </td>
        <td>
          <input type="text" v-model="secondTopic" />
          <button v-on:click="addSecond">Add</button>
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
import ComparisonService from "../services/ComparisonService";
import ApiService from "../services/ApiService";

export default {
  name: "ComparisonTable",
  data: () => ({
    edit: false,
    first: {},
    second: {},
    firstTopic: "",
    commonTopic: "",
    secondTopic: "",
    technologies: []
  }),
  methods: {
    toggleEdit() {
      this.edit = !this.edit;
    },
    uniqueFirst() {
      return ComparisonService.getUniqueFirst(this.first, this.second);
    },
    common() {
      return ComparisonService.getCommon(this.first, this.second);
    },
    uniqueSecond() {
      return ComparisonService.getUniqueSecond(this.first, this.second);
    },
    async addFirst() {
      await ApiService.addTopicToTech({
        title: this.firstTopic,
        technology: this.first.url
      });
      this.first = await ApiService.get(this.first.url);
      this.firstTopic = "";
    },
    async addCommon() {
      await ApiService.addTopicToTech({
        title: this.commonTopic,
        technology: this.first.url
      });
      await ApiService.addTopicToTech({
        title: this.commonTopic,
        technology: this.second.url
      });
      this.first = await ApiService.get(this.first.url);
      this.second = await ApiService.get(this.second.url);
      this.commonTopic = "";
    },
    async addSecond() {
      await ApiService.addTopicToTech({
        title: this.secondTopic,
        technology: this.second.url
      });
      this.second = await ApiService.get(this.second.url);
      this.secondTopic = "";
    }
  },
  async mounted() {
    this.technologies = await ApiService.allTechnologies();
  }
};
</script>

<style scoped>
table {
  width: 100%;
}

td {
  border-bottom: 1px solid black;
}

.content {
  height: 45vh;
}

.content td {
  width: 33%;
}

.content td:nth-child(n + 2),
.edit td:nth-child(n + 2) {
  border-left: 1px solid black;
}

.edit-button {
  position: fixed;
  top: 20px;
  right: 25px;
}
</style>