import intersectionBy from 'lodash/intersectionBy'
import xorBy from 'lodash/xorBy'

export default class ComparisonService {

    static getUniqueFirst(first, second) {
        return xorBy(first.topics, ComparisonService.getCommon(first, second), 'title')
    }

    static getUniqueSecond(first, second) {
        return xorBy(second.topics, this.getCommon(first, second), 'title')
    }
    static getCommon(first, second) {
        return intersectionBy(first.topics, second.topics, 'title')
    }
}